<template>
  <div class="map_wrap">
    <!-- Í≤∞Í≥ºÍ∞í Î™®Îã¨Ï∞Ω -->
    <div class="modal">
      <div class="black_bg">
        <div class="white_bg">
          <h4>üîî ÎèÑÏ∞©Ìïú Ï†êÏã¨ Î©îÎâ¥Îäî?</h4>
          <div class="resultMenu">
            <div>
              <ul id="StoreNameList"></ul>
            </div>
            <div id="pickMenu"></div>
          </div>
          <button id="CloseBtn" class="btn btn-danger btn">Ï∞Ω Îã´Í∏∞</button>
        </div>
      </div>
    </div>
    <div id="map"></div>
    <div>
      <input
        type="text"
        class="col form-control"
        v-model.trim="keyWord"
        @keyup.enter="keyWordSearch"
      />
      <button
        type="submit"
        class="col col-lg-2 btn-success"
        @click="keyWordSearch"
      >
        Í≤ÄÏÉâ
      </button>
      <button id="SlotBtn" class="btn btn-danger btn">Î©îÎâ¥ Î∞úÏÇ¨</button>
    </div>
    <div id="menu_wrap">
      <ul id="placesList"></ul>
      <div id="pagination"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "KakaoMap",
  data() {
    return {
      keyWord: "ÎßõÏßë",
    };
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=1979f702d77ea0046b74ea7bdfbea8df&libraries=services";
      document.body.appendChild(script);
    }
  },
  methods: {
    initMap() {
      let container = document.getElementById("map");
      let options = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 3,
      };

      //ÏßÄÎèÑ Í∞ùÏ≤¥Î•º Îì±Î°ùÌï©ÎãàÎã§.
      //ÏßÄÎèÑ Í∞ùÏ≤¥Îäî Î∞òÏùëÌòï Í¥ÄÎ¶¨ ÎåÄÏÉÅÏù¥ ÏïÑÎãàÎØÄÎ°ú initMapÏóêÏÑú ÏÑ†Ïñ∏Ìï©ÎãàÎã§.
      let map = new kakao.maps.Map(container, options);

      // HTML5Ïùò geolocationÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§
      if (navigator.geolocation) {
        // GeoLocationÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Ï†ëÏÜç ÏúÑÏπòÎ•º ÏñªÏñ¥ÏòµÎãàÎã§
        navigator.geolocation.getCurrentPosition(function (position) {
          var lat = position.coords.latitude; // ÏúÑÎèÑ
          var lon = position.coords.longitude; // Í≤ΩÎèÑ
          var locPosition = new kakao.maps.LatLng(lat, lon); // ÎßàÏª§Í∞Ä ÌëúÏãúÎê† ÏúÑÏπòÎ•º geolocationÏúºÎ°ú ÏñªÏñ¥Ïò® Ï¢åÌëúÎ°ú ÏÉùÏÑ±Ìï©ÎãàÎã§
          map.setCenter(locPosition);
          let marker = new kakao.maps.Marker({
            position: map.setCenter(locPosition), // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
          });
          marker.setMap(map);
        });
        this.keyWordSearch();
      } else {
        // HTML5Ïùò GeoLocationÏùÑ ÏÇ¨Ïö©Ìï† Ïàò ÏóÜÏùÑÎïå ÎßàÏª§ ÌëúÏãú ÏúÑÏπòÏôÄ Ïù∏Ìè¨ÏúàÎèÑÏö∞ ÎÇ¥Ïö©ÏùÑ ÏÑ§Ï†ïÌï©ÎãàÎã§
        var locPosition = new kakao.maps.LatLng(33.450701, 126.570667);
        map.setCenter(locPosition);
        let marker = new kakao.maps.Marker({
          position: map.setCenter(locPosition), // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
        });
        alert("ÏúÑÏπò Ï†ïÎ≥¥Î•º ÌôïÏù∏Ìï† Ïàò ÏóÜÏäµÎãàÎã§.");
        marker.setMap(map);
        this.keyWordSearch();
      }
    },
    keyWordSearch() {
      let markers = [];
      let mapContainer = document.getElementById("map"); // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† div
      let mapOption = {
        center: new kakao.maps.LatLng(33.450701, 126.570667), // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
        level: 3, // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
      };
      // ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      let map = new kakao.maps.Map(mapContainer, mapOption);
      let keyWordTemp = this.keyWord;

      // HTML5Ïùò geolocationÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§
      if (navigator.geolocation) {
        // GeoLocationÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Ï†ëÏÜç ÏúÑÏπòÎ•º ÏñªÏñ¥ÏòµÎãàÎã§
        navigator.geolocation.getCurrentPosition(function (position) {
          var lat = position.coords.latitude; // ÏúÑÎèÑ
          var lon = position.coords.longitude; // Í≤ΩÎèÑ
          var locPosition = new kakao.maps.LatLng(lat, lon); // ÎßàÏª§Í∞Ä ÌëúÏãúÎê† ÏúÑÏπòÎ•º geolocationÏúºÎ°ú ÏñªÏñ¥Ïò® Ï¢åÌëúÎ°ú ÏÉùÏÑ±Ìï©ÎãàÎã§

          // ÌÇ§ÏõåÎìúÎ°ú Ïû•ÏÜåÎ•º Í≤ÄÏÉâÌï©ÎãàÎã§
          searchPlaces(keyWordTemp, locPosition);
        });
      } else {
        alert("ÏúÑÏπò Ï†ïÎ≥¥Î•º ÌôïÏù∏Ìï† Ïàò ÏóÜÏäµÎãàÎã§.");
      }
      // Ïû•ÏÜå Í≤ÄÏÉâ Í∞ùÏ≤¥Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      let ps = new kakao.maps.services.Places();

      // Í≤ÄÏÉâ Í≤∞Í≥º Î™©Î°ùÏù¥ÎÇò ÎßàÏª§Î•º ÌÅ¥Î¶≠ÌñàÏùÑ Îïå Ïû•ÏÜåÎ™ÖÏùÑ ÌëúÏ∂úÌï† Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      let infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });

      // ÌÇ§ÏõåÎìú Í≤ÄÏÉâÏùÑ ÏöîÏ≤≠ÌïòÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function searchPlaces(keyword, locPosition) {
        console.log("Í≤ÄÏÉâÌò∏Ï∂ú", keyword);
        if (!keyword.replace(/^\s+|\s+$/g, "")) {
          alert("ÌÇ§ÏõåÎìúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî!");
          return false;
        }
        // Ïû•ÏÜåÍ≤ÄÏÉâ Í∞ùÏ≤¥Î•º ÌÜµÌï¥ ÌÇ§ÏõåÎìúÎ°ú Ïû•ÏÜåÍ≤ÄÏÉâÏùÑ ÏöîÏ≤≠Ìï©ÎãàÎã§
        ps.keywordSearch(keyword, placesSearchCB, {
          radius: 700,
          location: locPosition,
        });
      }

      // Ïû•ÏÜåÍ≤ÄÏÉâÏù¥ ÏôÑÎ£åÎêêÏùÑ Îïå Ìò∏Ï∂úÎêòÎäî ÏΩúÎ∞±Ìï®Ïàò ÏûÖÎãàÎã§
      function placesSearchCB(data, status, pagination) {
        if (status === kakao.maps.services.Status.OK) {
          // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Í≤ÄÏÉâÏù¥ ÏôÑÎ£åÎêêÏúºÎ©¥
          // Í≤ÄÏÉâ Î™©Î°ùÍ≥º ÎßàÏª§Î•º ÌëúÏ∂úÌï©ÎãàÎã§
          displayPlaces(data);
          storeListSave(data);
          // ÌéòÏù¥ÏßÄ Î≤àÌò∏Î•º ÌëúÏ∂úÌï©ÎãàÎã§
          displayPagination(pagination);
        } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
          displayPlaces(data);
          alert("Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
          return;
        } else if (status === kakao.maps.services.Status.ERROR) {
          displayPlaces(data);
          alert("Í≤ÄÏÉâ Í≤∞Í≥º Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
          return;
        }
      }

      // Í≤ÄÏÉâ Í≤∞Í≥º Î™©Î°ùÍ≥º ÎßàÏª§Î•º ÌëúÏ∂úÌïòÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function displayPlaces(places) {
        let listEl = document.getElementById("placesList");
        let menuEl = document.getElementById("menu_wrap");
        let fragment = document.createDocumentFragment();
        let bounds = new kakao.maps.LatLngBounds();
        let storeList = [];

        // Í≤ÄÏÉâ Í≤∞Í≥º Î™©Î°ùÏóê Ï∂îÍ∞ÄÎêú Ìï≠Î™©Îì§ÏùÑ Ï†úÍ±∞Ìï©ÎãàÎã§
        removeAllChildNods(listEl);

        // ÏßÄÎèÑÏóê ÌëúÏãúÎêòÍ≥† ÏûàÎäî ÎßàÏª§Î•º Ï†úÍ±∞Ìï©ÎãàÎã§
        removeMarker();

        for (let i = 0; i < places.length; i++) {
          // ÎßàÏª§Î•º ÏÉùÏÑ±ÌïòÍ≥† ÏßÄÎèÑÏóê ÌëúÏãúÌï©ÎãàÎã§
          let placePosition = new kakao.maps.LatLng(places[i].y, places[i].x);
          let marker = addMarker(placePosition, i);
          let itemEl = getListItem(i, places[i]); // Í≤ÄÏÉâ Í≤∞Í≥º Ìï≠Î™© ElementÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
          // Í≤ÄÏÉâÎêú Ïû•ÏÜå ÏúÑÏπòÎ•º Í∏∞Ï§ÄÏúºÎ°ú ÏßÄÎèÑ Î≤îÏúÑÎ•º Ïû¨ÏÑ§Ï†ïÌïòÍ∏∞ÏúÑÌï¥
          // LatLngBounds Í∞ùÏ≤¥Ïóê Ï¢åÌëúÎ•º Ï∂îÍ∞ÄÌï©ÎãàÎã§
          // let ListTemp = [];
          bounds.extend(placePosition);
          storeList.push(places[i].place_name); // eslint-disable-line no-unused-vars
          // ÎßàÏª§ÏôÄ Í≤ÄÏÉâÍ≤∞Í≥º Ìï≠Î™©Ïóê mouseover ÌñàÏùÑÎïå
          // Ìï¥Îãπ Ïû•ÏÜåÏóê Ïù∏Ìè¨ÏúàÎèÑÏö∞Ïóê Ïû•ÏÜåÎ™ÖÏùÑ ÌëúÏãúÌï©ÎãàÎã§
          // mouseout ÌñàÏùÑ ÎïåÎäî Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º Îã´ÏäµÎãàÎã§
          (function (marker, title) {
            kakao.maps.event.addListener(marker, "mouseover", function () {
              displayInfowindow(marker, title);
            });

            kakao.maps.event.addListener(marker, "mouseout", function () {
              infowindow.close();
            });

            itemEl.onmouseover = function () {
              displayInfowindow(marker, title);
            };

            itemEl.onmouseout = function () {
              infowindow.close();
            };
          })(marker, places[i].place_name);
          fragment.appendChild(itemEl);
        }

        // Í≤ÄÏÉâÍ≤∞Í≥º Ìï≠Î™©Îì§ÏùÑ Í≤ÄÏÉâÍ≤∞Í≥º Î™©Î°ù ElementÏóê Ï∂îÍ∞ÄÌï©ÎãàÎã§
        listEl.appendChild(fragment);
        menuEl.scrollTop = 0;

        // Í≤ÄÏÉâÎêú Ïû•ÏÜå ÏúÑÏπòÎ•º Í∏∞Ï§ÄÏúºÎ°ú ÏßÄÎèÑ Î≤îÏúÑÎ•º Ïû¨ÏÑ§Ï†ïÌï©ÎãàÎã§
        map.setBounds(bounds);
      }

      // Í≤ÄÏÉâÍ≤∞Í≥º Ìï≠Î™©ÏùÑ ElementÎ°ú Î∞òÌôòÌïòÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function getListItem(index, places) {
        var el = document.createElement("li"),
          itemStr =
            '<span class="markerbg marker_' +
            (index + 1) +
            '"></span>' +
            '<div class="info">' +
            '<h5 class="storeName">' +
            places.place_name +
            "</h5>";
        if (places.road_address_name) {
          itemStr +=
            "    <span>" +
            places.road_address_name +
            "</span>" +
            '   <span class="jibun gray"> üè† : ' +
            places.address_name +
            "</span>";
        } else {
          itemStr += "    <span>" + places.address_name + "</span>";
        }

        itemStr +=
          '  <span class="tel"> üìû : ' + places.phone + "</span>" + "</div>";

        el.innerHTML = itemStr;
        el.className = "item";
        return el;
      }

      // Î™®Îã¨Ï∞ΩÏóê Í≤ÄÏÉâ Îêú ÏãùÎãπ Î¶¨Ïä§Ìä∏ ÏÉùÏÑ±
      function storeListSave(storeList) {
        let StoreNameList = document.getElementById("StoreNameList");
        let pickMenu = document.getElementById("pickMenu");
        let storeName = [];
        let Storetemp = ""; //ÏûÑÏãú Î≥ÄÏàò ÏÑ†Ïñ∏ + Ï¥àÍ∏∞Ìôî

        for (let i = 0; i < storeList.length; i++) {
          storeName.push(storeList[i].place_name); // eslint-disable-line no-unused-vars
        }
        for (let x in storeName) {
          Storetemp += `<li>${storeName[x]}</li>`; // eslint-disable-line no-unused-vars
        }
        StoreNameList.innerHTML = Storetemp;

        // ÎûúÎç§ Ï∂îÏ≤® Í≤∞Í≥º ÏûëÏÑ±
        let SlotBtn = document.getElementById("SlotBtn");
        let CloseBtn = document.getElementById("CloseBtn");
        let modalWindow = document.querySelector(".modal");

        // Î©îÎâ¥ Î∞úÏÇ¨ Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú
        SlotBtn.onclick = function () {
          modalWindow.className = "modal display";
          pickMenu.innerText = storePick(storeList);
        };

        // Îã´Í∏∞ Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú
        CloseBtn.onclick = function () {
          modalWindow.className = "modal";
        };
      }

      function storePick(storeList) {
        let prePicked = storeList;
        let picked = [];
        if (storeList.length > 1) {
          // ÎûúÎç§ Ï∂îÏ≤®
          let random = Math.floor(Math.random() * storeList.length);
          // Ïù¥Ï†Ñ Í∞íÍ≥º Í∞ôÏúºÎ©¥ Îã§Î•º ÎïåÍπåÏßÄ Í≥ÑÏÜç ÎûúÎç§ Ïàò ÏÉùÏÑ±
          while (random === prePicked) {
            random = Math.floor(Math.random() * storeList.length);
          }
          // Ïù¥Ï†Ñ Ïà´ÏûêÏóê ÌòÑÏû¨ ÎûúÎç§ Ïàò ÎçîÌï¥Ï§å
          prePicked = random;
          // Ï∂îÏ≤® ÏôÑÎ£å
          picked = storeList[random];
        } else {
          //Î©îÎâ¥Í∞Ä ÏóÜÍ±∞ÎÇò 2Í∞ú ÎØ∏ÎßåÏù∏ Í≤ΩÏö∞
          window.alert(
            "Î∞úÏÇ¨Í∞Ä Ï§ëÏßÄ ÎêòÏóàÏäµÎãàÎã§! \nÎ©îÎâ¥Î•º Ï∂îÍ∞Ä ÌïòÏãúÍ±∞ÎÇò, Ï≤´ Î¶¨Ïä§Ìä∏Î°ú Ï¥àÍ∏∞Ìôî Ìï¥Ï£ºÏÑ∏Ïöî."
          );
        }
        return picked.place_name;
      }

      // ÎßàÏª§Î•º ÏÉùÏÑ±ÌïòÍ≥† ÏßÄÎèÑ ÏúÑÏóê ÎßàÏª§Î•º ÌëúÏãúÌïòÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function addMarker(position, idx) {
        var imageSrc = "./marker_number_rocket.png", // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄ url, Ïä§ÌîÑÎùºÏù¥Ìä∏ Ïù¥ÎØ∏ÏßÄÎ•º ÏîÅÎãàÎã§
          imageSize = new kakao.maps.Size(36, 37), // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò ÌÅ¨Í∏∞
          imgOptions = {
            spriteSize: new kakao.maps.Size(36, 691), // Ïä§ÌîÑÎùºÏù¥Ìä∏ Ïù¥ÎØ∏ÏßÄÏùò ÌÅ¨Í∏∞
            spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), // Ïä§ÌîÑÎùºÏù¥Ìä∏ Ïù¥ÎØ∏ÏßÄ Ï§ë ÏÇ¨Ïö©Ìï† ÏòÅÏó≠Ïùò Ï¢åÏÉÅÎã® Ï¢åÌëú
            offset: new kakao.maps.Point(13, 37), // ÎßàÏª§ Ï¢åÌëúÏóê ÏùºÏπòÏãúÌÇ¨ Ïù¥ÎØ∏ÏßÄ ÎÇ¥ÏóêÏÑúÏùò Ï¢åÌëú
          },
          markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imgOptions
          ),
          marker = new kakao.maps.Marker({
            position: position, // ÎßàÏª§Ïùò ÏúÑÏπò
            image: markerImage,
          });

        marker.setMap(map); // ÏßÄÎèÑ ÏúÑÏóê ÎßàÏª§Î•º ÌëúÏ∂úÌï©ÎãàÎã§
        markers.push(marker); // Î∞∞Ïó¥Ïóê ÏÉùÏÑ±Îêú ÎßàÏª§Î•º Ï∂îÍ∞ÄÌï©ÎãàÎã§
        return marker;
      }

      // ÏßÄÎèÑ ÏúÑÏóê ÌëúÏãúÎêòÍ≥† ÏûàÎäî ÎßàÏª§Î•º Î™®Îëê Ï†úÍ±∞Ìï©ÎãàÎã§
      function removeMarker() {
        for (var i = 0; i < markers.length; i++) {
          markers[i].setMap(null);
        }
        markers = [];
      }

      // Í≤ÄÏÉâÍ≤∞Í≥º Î™©Î°ù ÌïòÎã®Ïóê ÌéòÏù¥ÏßÄÎ≤àÌò∏Î•º ÌëúÏãúÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function displayPagination(pagination) {
        var paginationEl = document.getElementById("pagination"),
          fragment = document.createDocumentFragment(),
          i;

        // Í∏∞Ï°¥Ïóê Ï∂îÍ∞ÄÎêú ÌéòÏù¥ÏßÄÎ≤àÌò∏Î•º ÏÇ≠Ï†úÌï©ÎãàÎã§
        while (paginationEl.hasChildNodes()) {
          paginationEl.removeChild(paginationEl.lastChild);
        }

        for (i = 1; i <= pagination.last; i++) {
          var el = document.createElement("a");
          el.href = "#";
          el.innerHTML = i;

          if (i === pagination.current) {
            el.className = "on";
          } else {
            el.onclick = (function (i) {
              return function () {
                pagination.gotoPage(i);
              };
            })(i);
          }

          fragment.appendChild(el);
        }
        paginationEl.appendChild(fragment);
      }
      function displayInfowindow(marker, title) {
        var content = '<div style="padding:5px;z-index:1;">' + title + "</div>";

        infowindow.setContent(content);
        infowindow.open(map, marker);
      }
      // Í≤ÄÏÉâÍ≤∞Í≥º Î™©Î°ùÏùò ÏûêÏãù ElementÎ•º Ï†úÍ±∞ÌïòÎäî Ìï®ÏàòÏûÖÎãàÎã§
      function removeAllChildNods(el) {
        while (el.hasChildNodes()) {
          el.removeChild(el.lastChild);
        }
      }
    },
  },
};
</script>
